.about-kitchen {
    &__title {
        @include visually-hidden;
    }

    &__container {
      position: relative;

      padding-top: 16px;
      padding-bottom: 20px;

      background-image: linear-gradient(to right,
        $bgrColorLemon 33.81%,
        rgba(186, 186, 186, 0.4) 33.81%,
        rgba(186, 186, 186, 0.4) 34.06%,
        $bgrColorLemon 34.06%,
        $bgrColorLemon 65.94%,
        rgba(186, 186, 186, 0.4) 65.94%,
        rgba(186, 186, 186, 0.4) 66.18%,
        $bgrColorLemon 66.18%);

      @include for-tablet-landscape-up {
        padding-top: 120px;
        padding-bottom: 262px;

        background-image: linear-gradient(to right,
          $bgrColorLemon 25.57%,
          rgba(186, 186, 186, 0.4) 25.57%,
          rgba(186, 186, 186, 0.4) 25.67%,
          $bgrColorLemon 25.67%,
          $bgrColorLemon 50%,
          rgba(186, 186, 186, 0.4) 50%,
          rgba(186, 186, 186, 0.4) 50.1%,
          $bgrColorLemon 50.1%,
          $bgrColorLemon 74.32%,
          rgba(186, 186, 186, 0.4) 74.32%,
          rgba(186, 186, 186, 0.4) 74.42%,
          $bgrColorLemon 74.42%);
      }

      @include for-desktop-up {
        padding-bottom: 225px;

        background-image: linear-gradient(to right,
          $bgrColorLemon 23.51%,
          rgba(186, 186, 186, 0.4) 23.51%,
          rgba(186, 186, 186, 0.4) 23.59%,
          $bgrColorLemon 23.59%,
          $bgrColorLemon 50%,
          rgba(186, 186, 186, 0.4) 50%,
          rgba(186, 186, 186, 0.4) 50.09%,
          $bgrColorLemon 50.09%,
          $bgrColorLemon 76.4%,
          rgba(186, 186, 186, 0.4) 76.4%,
          rgba(186, 186, 186, 0.4) 76.49%,
          $bgrColorLemon 76.49%);
      }
    }

    &__slogan {
        margin-bottom: 12px;
        font-family: 'Times New Roman';
        font-size: 20px;
        line-height: 1.15;
        color: $thirdTextColor;

        @include for-tablet-landscape-up {
            font-size: 32px;
            margin-bottom: 4px;
        }
    }

    &__subtitle {
        margin-bottom: 12px;
        font-family: 'Film P3';
        font-weight: 700;
        font-size: 40px;
        line-height: 1.08;
        letter-spacing: 0.3em;
        color: $fifthTextColor;

        @include for-tablet-landscape-up {
            font-size: 64px;
            margin-bottom: 24px;
        }
    }

    &__text {
        max-width: 258px;
        margin-bottom: 28px;

        font-weight: 600;
        font-size: 12px;
        line-height: 1.25;
        letter-spacing: 0.02em;
        color: $primaryTextColor;

        @include for-tablet-landscape-up {
            font-size: 16px;
            margin-bottom: 142px;
            max-width: 440px;
        }

        @include for-desktop-up {
            max-width: 518px;
            margin-bottom: 162px;
        }
    }

    &__primary-image {
        @include img;
        margin-left: auto;
        margin-bottom: 32px;
        box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
        border-radius: 2px;

        @include for-tablet-landscape-up {
            position: absolute;
            top: -32px;
            right: 80px;
            margin-bottom: 0;
            box-shadow: 5px 5px 70px 17px rgba(0, 0, 0, 0.25);
        }

        @include for-desktop-up {
            right: 110px;
        }
    }

    &__block {
        display: flex;
        gap: 20px;
        margin-bottom: 32px;
        align-items: center;

        @include for-tablet-portrait-up {
            display: flex;
            gap: 20px;
            align-items: center;
        }

        @include for-tablet-landscape-up {
            gap: 48px;
            margin-bottom: 0;
        }

        @include for-desktop-up {
            gap: 32px;
        }
    }

    &__secondary-image {
        @include img;
        box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
        border-radius: 2px;

        @include for-tablet-landscape-up {
            box-shadow: 5px 5px 70px 17px rgba(0, 0, 0, 0.25);
        }
    }

    &__description {
        transform: translateY(-9px);
        font-weight: 600;
        font-size: 12px;
        line-height: 1.25;
        letter-spacing: 0.02em;
        color: $brownColor;
        max-width: 312px;

        @include for-tablet-landscape-up {
            font-size: 16px;
            transform: translateY(7px);
        }

        @include for-desktop-up {
            transform: translateY(-40px);
        }
    }

    &__swiper {
        margin-left: 70px;

        @include for-tablet-landscape-up {
            position: absolute;
            right: 28px;
            bottom: 32px;
            width: 428px;
            margin-left: 0;
        }

        @include for-desktop-up {
            right: 15;
            bottom: 50px;
            width: 548px;
        }
    }
}

.about-kitchen-swiper {
    &__slider {
        margin-bottom: 20px;

        @include for-tablet-landscape-up {
            margin-bottom: 32px;
        }

        @include for-desktop-up {
            margin-bottom: 36px;
        }
    }

    &__slide {
        @include for-phone-only {
            max-width: 245px;
        }

        @include for-tablet-portrait-up {
            width: 245px;
        }

        @include for-tablet-landscape-up {
            width: 194px;
        }

        @include for-desktop-up {
            width: 254px;
        }
    }

    &__image {
        @include img;
        // box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
        border-radius: 2px;
    }

    &__buttons {
        display: flex;
        flex-direction: row-reverse;
        gap: 32px;
        padding-right: 2px;
    }

    &__button {
        padding: 0;
        border: none;
        background-color: transparent;
        color: $primaryBlack;
        cursor: pointer;
        transition: color 250ms linear;

        &:hover,
        &:active,
        &:focus-visible {
            color: $accentColor;
        }
    }
}

.swiper__icon {
    fill: currentColor;
}